import React, { Component } from 'react';
import mod from './mod'
import './main.css'
import {Card,Button,CardGroup,Image,Form} from 'react-bootstrap'
import {storage} from './firebase'
class UserDash extends Component {
  constructor(props){
    super(props);
    this.state={
      ac:false,
      cu:null,
      page_load:null,
      change_to:null,
      item_name:null,
      item_price:null,
      poster_email:null,
      uploded_img:null,
      img_url:null

    }
    this.FileSelectedHandler = this.FileSelectedHandler.bind(this)
    this.fileUploadHandler = this.fileUploadHandler.bind(this)
  }




  change_name_handler(){
    const val = this.refs.username.value
    console.log(this.state.cu)
    this.setState({
      change_to:val
    })
    console.log(this.state.change_to)
  }


  add_item_name(){
    const val = this.refs.itemname.value

    console.log(this.state.item_name)

    this.setState({
      item_name:val
    })
}

  add_item_price(){
    const val = this.refs.itemprice.value



    this.setState({
      item_price:val
    })
    console.log(this.state.item_price);
  }



  change_name_handler_button(){
    if (this.state.change_to == undefined){
      console.log('name cant be empty')
    }else{
      (async () => {
    const rawResponse = await fetch(`http://localhost:5000/user/${this.state.cu}`, {
      method: 'PATCH',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({username:this.state.change_to})
    });
    const content = await rawResponse.json();

    console.log(content);
    console.log(this.state.cu)
    console.log(this.state.change_to);
  })();
    }
  }

  FileSelectedHandler = event => {
    if(event.target.files[0]){
      const img = event.target.files[0];
      this.setState({
        uploded_img:img
      })




    }



  }


  fileUploadHandler(){
   const {uploded_img} = this.state
   console.log(uploded_img)
   const UploadTask = storage.ref(`prodimg/${uploded_img.name}`).put(uploded_img)
    UploadTask.on('state_changed',
   (snapshot)=>{

   },
   (error)=>{
     console.log(error);

   },
 ()=>{
   storage.ref('prodimg').child(uploded_img.name).getDownloadURL().then(url=>{

    this.setState({
      img_url:url
    })
   })

 })
 }

  add_item_button_handler(){
    console.log(this.state.item_name)
    console.log(this.state.item_price)
    console.log(this.state.cu)

    if (this.state.img_url == undefined){
      console.log('Loading omg')
    }else{
      (async () => {
    const rawResponse = await fetch(`http://localhost:5000/user/add/${this.state.cu}`, {
      method: 'PATCH',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({instore:{"itemname":this.state.item_name,"itemprice":this.state.item_price,"postedby":this.state.cu,"posteremail":this.state.poster_email,"itemimg":this.state.img_url}})
    });
    const content = await rawResponse.json();

    console.log(content);

  })();
    }

  }


  componentDidMount() {
    const l = localStorage
    console.log(l);
    var current_name = l.getItem('username')
    var current_email = l.getItem('email')
    if (current_name == undefined ){
      this.setState({
        page_load:'hide'
      })
    }

    console.log(current_name);
    this.setState({
      cu:current_name,
      poster_email:current_email
    })



  }

  logimgpath(){
    console.log(this.state.uploded_img)
    console.log(this.state.img_url)
  }



  render() {
    const {cu,page_load} = this.state

    return (
      <>
      <div className={page_load}>
      <div className="menu-toggle" id="hamburger">
          <i className="fas fa-bars"></i>
      </div>
      <div className="overlay"></div>

      <div className="container">
          <nav>
              <h1 class="brand"><a href="/">Designer<span>H</span>up</a></h1>

              <ul>
                  <li><a href="/">Home</a></li>
                  <li><a href="/">{cu}</a></li>
                  <li><a href="/">perchescs</a></li>
              </ul>
          </nav>


      </div>

      <h5>Change user name</h5>
      <input name='username' ref='username' onChange={this.change_name_handler.bind(this)}/>



      <h6>Item Name</h6>

      <div><input name='itemname' ref='itemname' onChange={this.add_item_name.bind(this)}/></div>
      <h6>item price</h6>
      <div><input name='itemprice' ref='itemprice' onChange={this.add_item_price.bind(this)}/></div>
      <h6>Upload Image</h6>
      <input type="file" onChange={this.FileSelectedHandler}/>
      <button onClick={this.change_name_handler_button.bind(this)}>Save</button>
      <div><button onClick={this.add_item_button_handler.bind(this)}>SubItem</button></div>
      <div><Button onClick={this.logimgpath.bind(this)}>img path</Button></div>
      <div><Button onClick={this.fileUploadHandler} name='imgLink'>Upload</Button></div>
      </div>

      </>
    );
  }

}

export default UserDash;
