import React, { Component } from 'react';
import {Card,Button,CardGroup,Image,Row,Col,Container} from 'react-bootstrap'
import './cards.css'
class ShopCards extends Component {
  state={
    data:[]

  }


  componentDidMount() {
    fetch('http://localhost:5000/database')
    .then(res=>res.json())
    .then(res => this.setState({data:res.posts}))
  }





  render(){
    const {data} = this.state
    const {name,price} = this.props
    const postedby = []
    return (

      <>
      {
        data.map(x=>{
          var all_store_data = x.instore
          all_store_data.map(x=>{
            postedby.push(x)
          })
        })
      }
      {
        postedby.map(x=>{
          return <>
          <row>
          <div className="card">

          <div className="image">
             <img src={x.itemimg}/>
          </div>
          <div className="title">
           <h1>{x.itemname}</h1>
          </div>
          <div className="des">
           <h6>Posted by: {x.postedby}</h6>
           <h6>Price : {x.itemprice}</h6>
          <button>Buy Now</button><button>add to Cart</button>
          </div>
          </div>
          </row>
          </>
        })
      }

      </>
    );
  }

}

export default ShopCards;
